
import style from './base';

import { Link } from 'preact-router/match';
import { h, Component } from 'preact';
import ProjectPic from './projectpic';
import  config from '../../config'; 
const {ROOTDIR} = config;

export default class UCN extends Component {

    renderContent(){

        const container = {
            width: "100%",
            display:"flex",
            alignItems:"center",
            justifyContent:"center",
        }
        const imagestyle = {
            flex: "0 0 auto",
            width: 840,
            boxShadow: "-6px 5px 10px 3px rgba(0,0,0,0.09)",
        }
        const textcontainer ={
            marginBottom: 20,
        }

       return   <div>
                    <div style={textcontainer}>
                        The aim of the UCN project was to improve content recommendation for end users.  A singificant part of this was to gain an understanding of the ways that users consume digital content.  I worked with ethnographers and network researchers to build data visualisation and analysis tools that would allow users to annotate their digital footprints and help researchers gain insight into the complex network data captured from households.  I used <span class={style.tech}>python</span> for data analysis and <span class={style.tech}>d3.js</span> for interactive visualisations.   
                    </div>
                    <div style={container}>
                        <div style={imagestyle}> 
                            <img src={`${ROOTDIR}/ucn/dash.png`} style={{width:800}} />
                        </div>
                    </div>
                </div>
    }
    render({ item }) {
        const containerstyle = {
            background: item==="design" ? "lightgrey" : "white"
        } 

        const titlestyle ={
            textAlign: "center",
            fontFamily: "Futura,Trebuchet MS,Arial,sans-serif",
            fontSize: "2em",
            color: "white",
            textTransform: "uppercase",
            marginBottom: 0,
            padding: "10px 10px 0px 10px",
        }

        return (

           
            <div>
                <div>
                    <div class={style.summary}>
                        <div style={titlestyle}>ucn</div>
                        <div class={style.datecontainer}>
                            <div class={style.date}>
                                2014-2016
                            </div>
                        </div>

                        <div class={style.projectpic}><ProjectPic /></div>

                    </div>
                    <div class={style.linkbox}>
                        <div class={style.weblabel}>project site</div>
                        <div class={style.web}> www.usercentricnetworking.eu</div>
                    </div>
                </div>
                <div class={style.contentcontainer} style={containerstyle}>
                    <div class={style.menu}>
                        <Link activeClassName={style.active} class={style.back} href="/sc">←</Link>
                        <nav>
                            <Link activeClassName={style.active} href="/sc/project/ucn">about</Link>
                        </nav>
                    </div>
                    <div class={style.content}>


                        <div class={style.maincopy}>
                            {this.renderContent()}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}



